.home-container {
  height: 100vh;
  background-image: url("https://images.unsplash.com/photo-1501612780327-45045538702b?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1500&q=80");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.how-it-works-container {
  width: 100%;
  position: absolute;
  bottom: 10%;
  background-color: transparent;
  text-align: center;
}

.home-buttons-container {
  display: flex;
  justify-content: center;
}

.info-div {
  margin: auto;
  width: 60%;
  padding: 0 2rem;
}

@media screen and (max-width: 991px) {
  .info-div {
    width: 80%;
    padding: 0 1rem;
  }
}

@media screen and (max-width: 576px) {
  .info-div {
    width: 90%;
    padding: 0;
  }
}
