  .select-user-type-form {
    width: 70%;
    height: 30rem;
    margin-top: 0rem;
  }
}

@media screen and (max-width: 768px) {
  .user-signup-div,
  .venue-signup-div {
    width: 100%;
    border: none;
    height: 50%;
