    margin-bottom: 3rem;
  }
  .user-types-flex-container {
    display: block;
  }
  .select-user-type-form {
    height: 30rem;
    width: 90%;
    margin-top: 0rem;
  }
  .user-type-button {
    margin: 1rem 2rem;
  }
